.header-app {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0.5rem;

    background-color: silver;
    box-shadow: 0 0 5px darkgray;
}

.main-app {
    padding: 0.5rem;
}

.schedule {
    margin: auto;
}

.description {
    display: flex;
    justify-content: space-around;
    text-align: center;
    margin: 2rem;
}
.yoga,
.dance {
    padding: 2rem;
    border-radius: 10px;
}
.yoga {
    background-color: rgb(255, 194, 80);
}
.dance {
    background-color: rgb(253, 80, 80);
}

/* monday */
.ant-picker-content>thead>tr th:nth-child(1) {
    visibility: hidden;
    position: relative;
}
.ant-picker-content>thead>tr th:nth-child(1):after {
    visibility: visible;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    content: "Пон";
}

